"use client";

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ShieldCheck, User } from "lucide-react";
import { useLanguage } from "@/contexts/LanguageContext";

export function Navbar() {
    const { t, language, setLanguage } = useLanguage();

    return (
        <nav className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 sticky top-0 z-50">
            <div className="container flex h-16 items-center justify-between">
                <div className="flex items-center gap-2">
                    <ShieldCheck className="h-6 w-6 text-primary" />
                    <span className="text-xl font-bold tracking-tight">{t.nav.brand}</span>
                </div>

                <div className="hidden md:flex gap-6 items-center text-sm font-medium">
                    <Link href="/" className="hover:text-primary transition-colors">{t.nav.home}</Link>
                    <Link href="/chat" className="hover:text-primary transition-colors">{t.nav.ai}</Link>
                    <Link href="/calculator" className="hover:text-primary transition-colors">{t.nav.calc}</Link>
                    <Link href="/learn" className="hover:text-primary transition-colors">{t.nav.learn}</Link>
                </div>

                <div className="flex items-center gap-4">
                    <select
                        className="bg-transparent border rounded p-1 text-sm"
                        value={language}
                        onChange={(e) => setLanguage(e.target.value as "en" | "hi")}
                    >
                        <option value="en">English</option>
                        <option value="hi">हिंदी</option>
                    </select>
                    <Button variant="ghost" size="sm">
                        {t.nav.login}
                    </Button>
                    <Button size="sm">{t.nav.getStarted}</Button>
                </div>
            </div>
        </nav>
    );
}
